import React, { useEffect, useState } from "react";
import { Pagination } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { GetPageCount } from "../Actions/Actions";
const PaginationComponent = () => {
  const [page , setpage] = useState(0)
  const pages = useSelector((state)=>{
    return state.Popular.Count
   })
  const patch = useDispatch()
  useEffect(()=>{
    setpage(pages)
  },[page])
  
  const getpagewithnum = async (page) => {
    patch(GetPageCount(page))
  };
  const handlePageClick = (data) => {
    getpagewithnum(data + 1);
  };

  return  (
    <div className="container w-100 d-flex justify-content-center">
    <Pagination
      count={50}
      color="info"
      shape="rounded"
      
      onChange={(e) => {
        handlePageClick(parseInt(e.target.innerText));
      }}
    className=" py-2 rounded bg-light"
    />
    </div>
  ) 
};

export default PaginationComponent;