import React from 'react'
import {Form, FormControl} from "react-bootstrap"

import { useDispatch } from 'react-redux';
import { Actions,SearchFilter } from '../../Actions/Actions';
const PopularMovieInput = () => {
const onSearch = (word)=>{
  Search(word)
}
const patch = useDispatch()
  const Search = (values) => {
    if (values === "") {
      patch(Actions())
    } else {
      
     const time = setTimeout(async() => {
     patch(SearchFilter(values))
     }, 500)
     return ()=>{clearTimeout(time)};
    };
    }
  return (
    <div className='container d-flex justify-content-center w-100'>
    <Form className=" w-50" >
    <FormControl
      type="search"
      placeholder="Search In Popular Movies"
      className="me-2"
      style={{position:"relative"}}
      onChange={((e)=>{onSearch(e.target.value)})}
    />
   
  </Form>
  
    </div>
  )
}

export default PopularMovieInput