import React from 'react'
import {Navbar,Container,Nav,Form} from "react-bootstrap"
const NavBar = () => {
  return (
   
   <Navbar expand="lg" sticky="top" >
   <Container fluid>
     <Navbar.Toggle aria-controls="navbarScroll" />
     <Navbar.Collapse id="navbarScroll">
       <Nav className=" text-light fw-bolder rounded bg-light" >
         <Nav.Link href="#home" className="text-dark mx-2 p-3">Popular</Nav.Link>
         <Nav.Link href="#features" className="text-dark mx-2 p-3">TopRate</Nav.Link>
         <Nav.Link href="#pricing" className="text-dark mx-2 p-3">LaTest</Nav.Link>
         <Nav.Link href="#pricing" className="text-dark mx-2 p-3">InComing</Nav.Link>
       </Nav>
  
     </Navbar.Collapse>
   </Container>
 </Navbar>

  )
}

export default NavBar