import axios from "axios";
import { latest, LaTests, Movie, Popular, TopRate, TopRates } from "../Types/Types";
export const Actions =  () => {
 return async (dispatch)=>{
const Response = await axios.get(Popular);
dispatch({type:Movie , payload:Response.data.results,page:Response.data.total_pages})
}}



export const SearchFilter =  (word) => {
 return async (dispatch)=>{
const Response = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=04ed0ba7584fafa950b36f66bf22e10b&query=${word}&language=en-US&page=1&include_adult=false`);
dispatch({type:Movie , payload:Response.data.results,page:Response.data.total_pages})
}}
export const GetPageCount =  (page) => {
 return async (dispatch)=>{
const Response = await axios.get(`https://api.themoviedb.org/3/movie/upcoming?api_key=04ed0ba7584fafa950b36f66bf22e10b&language=ar-US&page=${page}`);
dispatch({type:Movie , payload:Response.data.results,page:Response.data.total_pages})
}}




export const RateData =  () => {
    return async (dispatch)=>{
   const Response = await axios.get(TopRate);
   dispatch({type:TopRates , payload:Response.data.results , page:Response.data.total_pages})
   }}
   export const LaTest =  () => {
    return async (dispatch)=>{
   const Response = await axios.get(LaTests);
   dispatch({type:latest , payload:Response.data.results , page:Response.data.total_pages })
   }}