import React, { Fragment } from "react";
import { useSelector } from "react-redux";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "../App.css";
import "swiper/css/navigation";
import "swiper/css/effect-coverflow";
import { EffectCoverflow, Navigation } from "swiper";
import PaginationComponent from "./PaginationPopular";
import PopularMovieInput from "./Inputs/PopularMovieInput";
import { NavLink } from "react-router-dom";

const PopularMovie = () => {
  const Select = useSelector((state) => {
    return state.Popular.movies;
  });
  const data = Select.map((e, inx) => {
    return (
      <SwiperSlide key={inx}>
        <NavLink to={`movie/${e.id}`} className="dicorations">
          <img
            src={`https://image.tmdb.org/t/p/w500/${e.poster_path}`}
            className="img-movie "
            alt="hu"
          />
          <div className="bg-light text-dark">
            <h5>{e.original_title}</h5>

            <h5>
              <i className="fas fa-calendar-alt"></i> {e.release_date}{" "}
            </h5>

            <h5>
              <i className="fas fa-star" style={{ color: "gold" }}></i>{" "}
              {e.vote_count}
            </h5>
          </div>
        </NavLink>
      </SwiperSlide>
    );
  });
  return (
    <Fragment>
      <h3 className="text-center p-lg-3 fw-bold">Popular Movies</h3>
      <PopularMovieInput />
      <PaginationComponent />
      <Swiper
        spaceBetween={0}
        navigation={true}
        effect={"coverflow"}
        grabCursor={true}
        centeredSlides={true}
        coverflowEffect={{
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
        }}
        breakpoints={{
          640: {
            slidesPerView: 3,
          },
          768: {
            slidesPerView: 4,
          },
          1024: {
            slidesPerView: 5,
          },
        }}
        modules={[EffectCoverflow, Navigation]}
        className="mySwiper"
      >
        {data}
      </Swiper>
    </Fragment>
  );
};

export default PopularMovie;
