import React, { useEffect, useState } from "react";
import axios from "axios";
import { Container, Col , Row } from "react-bootstrap";
import { useParams , NavLink } from "react-router-dom";

const Popularid = () => {
  const pram = useParams();
  const [data, setdata] = useState([]);

  const GetMovieid = async () => {
    const Response = await axios.get(
      `https://api.themoviedb.org/3/movie/${pram.id}?api_key=04ed0ba7584fafa950b36f66bf22e10b&language=ar-US`
    );

    setdata(Response.data);
  };
  useEffect(() => {
    GetMovieid();
  }, []);
  console.log(data);
  return (
    <Container className="bg-light" >
      <Row
        className="justify-content-space-around">
        <Col className="p-0 m-0 " md="3" xs="12" sm="12">
          <div className="">
            <img
              src={`https://image.tmdb.org/t/p/w500/${data.poster_path}`}
              className=""
              alt="hu"
              style={{ height: "100%", width: "100%" }}
            />
          </div>
        </Col>
        <Col
          className="d-flex align-items-center justify-content-center"
          md="8"
          xs="12"
          sm="12"
        >
          <div className=" my-2">
            <div>
              <div className="center text-center">
                <h4 className="my-2">Movie Name:{data.original_title}</h4>

                <h5 className="my-2">Date:{data.release_date}</h5>
                <h6 className="my-2">Vote:{data.vote_count} </h6>
                <h6 className="my-2">language:{data.original_language} </h6>
              </div>
            </div>
            <h3 className="text-center p-3">Story Movie</h3>
            <h6 className="my-4 text-center">{data.overview}</h6>
            <div className="my-5 d-flex justify-content-center align-items-center">
              <a
                href={data.homepage}
                target="_blank"
                rel="noreferrer"
                className="btn btn-outline-primary mx-2"
              >
                شاهد
              </a>
              <NavLink to="/" className="btn btn-outline-primary mx-2">
                الرئيسيه
              </NavLink>
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default Popularid;
