import React from 'react'
import {Form} from "react-bootstrap"
const TopRateMovieInput = () => {
  return (
    <div className='container d-flex justify-content-center w-100'>
    <Form className=" w-50" >
    <Form.Control
      type="search"
      placeholder="Search In Popular Movies"
      className="me-2"
      aria-label="Search"
      style={{position:"relative"}}
    />
   
  </Form>
  
    </div>
  )
}

export default TopRateMovieInput