import "./App.css";
import { useDispatch } from "react-redux";
import { useEffect } from "react";
import { Actions, LaTest, RateData } from "./Actions/Actions";
import PopularMovie from "./Components/PopularMovie";
import NavBar from "./Components/NavBar";
import TopRateMovie from "./Components/TopRateMovie";
import LaTestMovie from "./Components/LaTestMovie";
import Popularid from "./Components/Pagination/PopularID/Popularid";
import { BrowserRouter, Routes, Route } from "react-router-dom";

function App() {
  const patch = useDispatch();

  useEffect(() => {
    patch(Actions());
    patch(RateData());
    patch(LaTest());
  }, []);
  return (
    <div>
      <NavBar />
      <BrowserRouter>
        <Routes>
          <Route element={<PopularMovie />} path="/" />
          <Route element={<Popularid />} path="/movie/:id" />
        </Routes>
      </BrowserRouter>
      <TopRateMovie />
      <LaTestMovie />
    </div>
  );
}

export default App;
